body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*JUMBOTRON*/
.jumbotron {
	background-image: url("https://wallpapercave.com/wp/iDLlXUh.jpg");
    background-blend-mode: lighten;
    background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	margin-bottom: 0px;
    text-align: center;
}

.jumbotron h1 {
	font-family: 'Montserrat', sans-serif;
	font-size: 65px;
	text-transform: uppercase;
	font-weight: 900;
	color: #00ccff;
}

.jumbotron p {
	font-family: 'Montserrat', sans-serif;
	font-size: 18px;
	font-weight: 500;
	text-transform: uppercase;
}
.allCards {
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    text-align: center;
}

.allCards img {
    margin: 5px;
    padding: 20px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    max-height: 200px;
    max-width: 200px;
    background-color: white;
}
.mainCardContainer {
    margin-bottom: 50px;
    padding: 0; 
}

.gameMessage {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: #00ccff;
    color: white;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:#33cc33;
    color: white;
    padding: 20px 0;
    margin-bottom: 25px;
}
.mainContainer {
  padding: 0; 
  background-color: rgba(0, 0, 0, 0.03);
  min-height: 100vh;
  padding-bottom: 100px;
  }
